@media (max-width: 768px) {
	.item-det {

		ul.d-flex,
		.d-flex {
			display: block !important;
		}

		.rating-stars.d-flex {
			display: flex !important;
		}
	}
}

.item-card {
	.cardtitle {
		span {
			display  : block;
			font-size: .75rem;
		}

		a {
			color          : #1f252d;
			font-weight    : 500;
			text-decoration: none;
		}
	}

	.cardprice {
		position: absolute;
		top     : 30px;
		right   : 15px;

		span {
			&.type--strikethrough {
				opacity        : .7;
				text-decoration: line-through;
			}

			display: block;
			color  : #1f252d;
		}
	}

	.cardbody {
		position     : relative;
		padding      : 30px 15px;
		border-radius: 0 0 .2rem .2rem;
	}
}

@media (max-width: 400px) {
	.item-search-menu ul li a {
		width  : 100%;
		display: block;
		margin : 0 0 8px 0 !important;
	}
}

@media (max-width: 767px) {
	.item2-gl-nav {
		display: block !important;

		.item2-gl-menu li {
			text-align   : center;
			margin-bottom: 10px;
			margin       : 0 auto;
			padding      : 10px 0;
		}

		label {
			display   : block;
			float     : none;
			text-align: center;
			width     : 30%;
		}

		.d-flex {
			text-align: center;
			width     : 100%;
		}

		.form-control.select-sm.w-70 {
			width: 70% !important;
		}
	}
}

