.item-card {
	.item-card-desc:before {
		content      : '';
		display      : block;
		position     : absolute;
		background   : rgba(42, 16, 25, 0.5);
		width        : 100%;
		height       : 100%;
		right        : 0;
		top          : 0;
		z-index      : 1;
		border-radius: 5px;
	}

	overflow     : hidden;
	border-radius: 2px;
}

.item-card-desc {
	position: relative;

	a {
		position: absolute;
		top     : 0;
		left    : 0;
		bottom  : 0;
		width   : 100%;
		height  : 100%;
		z-index : 10;
	}
}

.item-card .item-card-desc .item-card-text {
	position  : absolute;
	top       : 42%;
	left      : 0;
	right     : 0;
	text-align: center;
	color     : $white;
	z-index   : 2;
}

.item-card-text {
	span {
		font-size  : 16px;
		display    : block;
		margin     : .5rem;
		font-weight: 400;
	}

	h4 {
		font-size  : 25px;
		font-weight: 300;
	}
}

.item-card {
	.item-card-btn {
		padding   : 0;
		margin    : 0;
		opacity   : 0;
		left      : 37.7%;
		text-align: center;
		position  : absolute;
		bottom    : -30px;
		z-index   : 1;
		transition: all .5s ease 0s;
	}

	&:hover {
		.item-card-btn {
			opacity: 1;
			bottom : 25%;
		}

		.data1 .item-card-img img {
			transform: scale(1);
		}
	}

	.item-card-btn a {
		display   : block;
		position  : relative;
		transition: all .3s ease-in-out;

		&:after,
		&:before {
			transform: translateX(-50%);
			position : absolute;
			left     : 50%;
			top      : -30px;
		}

		&:after {
			content      : '';
			height       : 15px;
			width        : 15px;
			border-radius: 0;
			transform    : translateX(-50%) rotate(45deg);
			top          : -20px;
			z-index      : -1;
		}
	}
}

