	display: none;
}

.overlapblackbg {
	opacity: 0;
	visibility: hidden;
}

.horizontalMenu {
	.horizontalMenu-click,
	.horizontalMenu-click02 {
		display: none;
	}
}

.hometext {
	display: none;
