/* ================== Mobile Menu Change Brake Point ================== */

@media only screen and (max-width: 991px) {
	body.active {
		overflow: hidden;
	}

	.desktoplogo {
		display: none;
	}

	.horizontal-main {
		height: 0;
		padding: 0;
	}

	.horizontalMenucontainer {
		background-attachment: local;
		background-position: 33% 0%;
	}

	.horizontalMenu {
		width: 100%;
		background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
		left: 0;
		overflow-y: hidden;
		padding: 0;
		top: 0;
		position: fixed;
		visibility: hidden;
		margin: 0px;

		> .horizontalMenu-list {
			height: auto;
			min-height: 100%;
			width: 240px;
			background: $white;
			padding-bottom: 0;
			margin-left: -240px;
			display: block;
			text-align: center;

			> li {
				width: 100%;
				display: block;
				float: none;
				border-right: none;
				background-color: transparent;
				position: relative;
				white-space: inherit;
				clear: right;
				padding: 0;

				span a {
					width: 85%;
					margin: 0 auto;
				}

				> a {
					padding: 12px 32px 12px 17px;
					font-size: 14px;
					text-align: left;
					border-right: solid 0px;
					color: $color;
					line-height: 25px;
					border-bottom: 1px solid;
					border-bottom-color: rgba(0, 0, 0, 0.13);
					position: static;
					margin: 0;
					border-radius: 0px;

					&.active {
						background: $primary;
						color: $white;
					}

					span.fa.fa-caret-down {
						display: none;
					}
				}

				a.menuhomeicon {
					padding-left: 17px;
					padding-right: 17px;
					border-top: solid 1px rgba(0, 0, 0, 0.13);
				}

				> a {
					> .fa {
						font-size: 16px;
						color: #bfbfbf;
					}

					.arrow:after {
						display: none;
					}
				}

				&:hover > a {
					text-decoration: none;
				}

				> {
					a > .hometext {
						display: inline-block;
					}

					ul.sub-menu {
						display: none;
						position: relative;
						top: 0px;
						background-color: $white;
						border: none;
						padding: 0px;
						opacity: 1;
						visibility: visible;
						-webkit-transform: none;
						-moz-transform: none;
						-ms-transform: none;
						-o-transform: none;
						transform: none;
						-webkit-transition: inherit;
						-moz-transition: inherit;
						transition: inherit;
						-webkit-transform-style: flat;
						transform-style: flat;

						> li {
							> a {
								line-height: 20px;
								font-size: 13px;
								padding: 10px 0px 10px 16px;
								color: #383838;
							}

							span + a {
								padding-right: 30px;
							}

							> a {
								> .fa {
									display: none;
								}

								&:hover {
									background-color: #f6f7fb;
									color: $color;
									text-decoration: none;
								}
							}
						}

						li:hover > a {
							background-color: $primary;
							color: $color;
						}

						> li > ul.sub-menu {
							width: 100%;
							position: static;
							left: 100%;
							top: 0;
							display: none;
							margin: 0px;
							padding: 0px;
							border: solid 0px;
							transform: none;
							opacity: 1;
							visibility: visible;
							-webkit-transform: none;
							-moz-transform: none;
							-ms-transform: none;
							-o-transform: none;
							transform: none;
							-webkit-transition: inherit;
							-moz-transition: inherit;
							transition: inherit;
							-webkit-transform-style: flat;
							transform-style: flat;

							> li {
								margin: 0px 0px 0px 0px;
								padding: 0px;
								position: relative;

								> a {
									line-height: 20px;
									font-size: 13px;
									padding: 10px 0px 10px 26px;
									color: #383838;
								}

								span + a {
									padding-right: 30px;
								}

								> a {
									> .fa {
										display: none;
									}

									&:hover {
										background-color: $background;
										color: $color !important;
										text-decoration: none;
									}

									&.active {
										color: $black;
									}
								}

								&:hover > a {
									color: $black;
								}

								> ul.sub-menu {
									width: 100%;
									position: static;
									left: 100%;
									top: 0;
									display: none;
									margin: 0px;
									padding: 0px;
									border: solid 0px;
									transform: none;
									opacity: 1;
									visibility: visible;
									-webkit-transform: none;
									-moz-transform: none;
									-ms-transform: none;
									-o-transform: none;
									transform: none;
									-webkit-transition: inherit;
									-moz-transition: inherit;
									transition: inherit;
									-webkit-transform-style: flat;
									transform-style: flat;

									> li {
										margin: 0px 0px 0px 0px;

										> a {
											line-height: 20px;
											font-size: 13px;
											padding: 10px 0px 10px 34px;
											color: #383838;
										}

										span + a {
											padding-right: 30px;
										}

										> a {
											> .fa {
												display: none;
											}

											&:hover {
												background-color: $background;
												color: $color;
												text-decoration: none;
											}

											&.active {
												color: $black;
											}
										}
									}
								}
							}
						}
					}

					.horizontal-megamenu {
						.container {
							padding: 0;
						}

						color: $color;
						display: none;
						position: relative;
						top: 0px;
						padding: 0px 0px;
						border: solid 0px;
						transform: none;
						opacity: 1;
						visibility: visible;
						-webkit-transform: none;
						-moz-transform: none;
						-ms-transform: none;
						-o-transform: none;
						transform: none;
						-webkit-transition: inherit;
						-moz-transition: inherit;
						transition: inherit;
						border-bottom: 1px solid rgba(0, 0, 0, 0.13);
						-webkit-transform-style: flat;
						transform-style: flat;

						&.halfmenu {
							width: 100%;
							margin: 0px;
							padding: 5px 0px 10px 0px;
						}

						.title {
							color: $color;
							font-size: 15px;
							padding: 10px 8px 10px 0px;
						}

						> ul {
							width: 100%;
							margin: 0px;
							padding: 0px;
							font-size: 13px;

							> li > a {
								padding: 9px 14px;
								line-height: normal;
								font-size: 13px;
								background-color: $background;
								color: $color;

								&:hover {
									background-color: $black;
								}
							}
						}

						ul li.title {
							line-height: 26px;
							color: $color;
							margin: 0px;
							font-size: 15px;
							padding: 7px 0px;
							background-color: transparent;
						}

						&.halfdiv {
							width: 100%;
						}

						.menu_form {
							padding: 5px 0px 62px 0px;
						}

						.show-grid div {
							margin: 0px;
						}

						.menu_form {
							input {
								&[type="button"],
								&[type="submit"] {
									width: 46%;
								}
							}

							textarea {
								min-height: 100px;
							}
						}
					}
				}
			}
		}
	}

	.horizontal-header {
		width: 100%;
		display: block;
		position: relative;
		top: 0;
		left: 0;
		z-index: 1;
		height: 54px;
		background-color: $white;
		text-align: center;
		-webkit-transition: all 0.25s ease-in-out;
		-moz-transition: all 0.25s ease-in-out;
		-o-transition: all 0.25s ease-in-out;
		-ms-transition: all 0.25s ease-in-out;
		transition: all 0.25s ease-in-out;
		box-shadow: 0 0 1px $black-3;
	}

	.active .horizontal-header {
		-webkit-transition: all 0.25s ease-in-out;
		-moz-transition: all 0.25s ease-in-out;
		-o-transition: all 0.25s ease-in-out;
		-ms-transition: all 0.25s ease-in-out;
		transition: all 0.25s ease-in-out;
	}

	.horizontal-header .smllogo {
		display: inline-block;
		margin-top: 11px;
	}

	.horizontalMenu > .horizontalMenu-list > li.rightmenu > .topmenusearch {
		width: 86%;
		margin: 7% 7%;
		padding: 0px;

		input {
			border-radius: 50px;

			&:focus {
				width: 100%;
			}
		}
	}

	.callusbtn {
		color: #a9a9a9;
		font-size: 21px;
		position: absolute;
		right: 5px;
		top: 0px;
		transition: all 0.4s ease-in-out 0s;
		z-index: 102;
		padding: 11px 14px;

		.fa {
			vertical-align: top;
			margin-top: 4px;
		}

		&:hover .fa {
			color: #a9a9a9;
		}
	}

	.animated-arrow {
		position: absolute;
		left: 0;
		top: 0;
		z-index: 102;
		-webkit-transition: all 0.4s ease-in-out;
		-moz-transition: all 0.4s ease-in-out;
		-o-transition: all 0.4s ease-in-out;
		-ms-transition: all 0.4s ease-in-out;
		transition: all 0.4s ease-in-out;
		cursor: pointer;
		padding: 16px 35px 16px 0px;
		margin: 11px 0 0 15px;
	}

	.animated-arrow span {
		cursor: pointer;
		height: 2px;
		width: 17px;
		background: #a9a9a9;
		position: absolute;
		display: block;
		content: "";

		&:before,
		&:after {
			cursor: pointer;
			height: 2px;
			width: 17px;
			background: #a9a9a9;
			position: absolute;
			display: block;
			content: "";
		}

		&:before {
			top: -7px;
			width: 23px;
		}

		&:after {
			bottom: -7px;
			width: 17px;
		}

		transition: all 500ms ease-in-out;

		&:before,
		&:after {
			transition: all 500ms ease-in-out;
		}
	}

	.active .animated-arrow {
		span {
			&:after {
				width: 23px;
				top: -3px;
			}

			background-color: transparent !important;

			&:before {
				top: -3px;
			}
		}

		&.active span:after {
			top: -3px;
		}

		span {
			&:before {
				transform: rotate(45deg);
				-moz-transform: rotate(45deg);
				-ms-transform: rotate(45deg);
				-o-transform: rotate(45deg);
				-webkit-transform: rotate(45deg);
				bottom: 0px;
			}

			&:after {
				transform: rotate(-45deg);
				-moz-transform: rotate(-45deg);
				-ms-transform: rotate(-45deg);
				-o-transform: rotate(-45deg);
				-webkit-transform: rotate(-45deg);
			}
		}
	}

	/* ================== Mobile Overlay/Drawer CSS ================== */
	.overlapblackbg {
		right: 0;
		width: calc(100% - 240px);
		height: 100vh;
		min-height: 100%;
		position: fixed;
		top: 0;
		opacity: 0;
		visibility: hidden;
		cursor: pointer;
	}

	.active .horizontalMenu .overlapblackbg {
		opacity: 1;
		visibility: visible;
		-webkit-transition: opacity 1.5s ease-in-out;
		transition: opacity 1.5s ease-in-out;
		-moz-transition: opacity 1.5s ease-in-out;
		-ms-transition: opacity 1.5s ease-in-out;
		-o-transition: opacity 1.5s ease-in-out;
	}

	.horizontalMenucontainer {
		-webkit-transition: all 0.25s ease-in-out;
		-moz-transition: all 0.25s ease-in-out;
		-o-transition: all 0.25s ease-in-out;
		-ms-transition: all 0.25s ease-in-out;
		transition: all 0.25s ease-in-out;
	}

	.active {
		.horizontalMenucontainer {
			margin-left: 240px;
			-webkit-transition: all 0.25s ease-in-out;
			-moz-transition: all 0.25s ease-in-out;
			-o-transition: all 0.25s ease-in-out;
			-ms-transition: all 0.25s ease-in-out;
			transition: all 0.25s ease-in-out;
		}

		.horizontalMenu {
			overflow-y: scroll;
			-webkit-overflow-scrolling: touch;
			visibility: visible;
			z-index: 1000;
			top: 0;
			height: 100% !important;

			> .horizontalMenu-list {
				-webkit-transition: all 0.25s ease-in-out;
				-moz-transition: all 0.25s ease-in-out;
				-o-transition: all 0.25s ease-in-out;
				-ms-transition: all 0.25s ease-in-out;
				transition: all 0.25s ease-in-out;
				margin-left: 0;
			}
		}
	}

	/* ================== Mobile Sub Menu Expander Arrows  ================== */
	.horizontalMenu > .horizontalMenu-list > li {
		> .horizontalMenu-click {
			height: 49px;
			position: absolute;
			top: 0;
			right: 0;
			display: block;
			cursor: pointer;
			width: 100%;
		}

		.horizontalMenu-click.ws-activearrow > i {
			transform: rotate(180deg);
		}

		> {
			.horizontalMenu-click > i {
				display: block;
				height: 23px;
				width: 25px;
				margin-top: 14px;
				margin-right: 10px;
				background-size: 25px;
				font-size: 21px;
				color: #ccc;
				float: right;
			}

			ul.sub-menu > li .horizontalMenu-click02 {
				height: 41px;
				position: absolute;
				top: 0;
				right: 0;
				display: block;
				cursor: pointer;
				width: 100%;

				> i {
					display: block;
					height: 23px;
					width: 25px;
					margin: 8px 6px 8px 6px;
					background-size: 25px;
					font-size: 21px;
					color: rgba(0, 0, 0, 0.25);
					float: right;

					&.horizontalMenu-rotate {
						-webkit-transform: rotate(180deg);
						-moz-transform: rotate(180deg);
						-ms-transform: rotate(180deg);
						-o-transform: rotate(180deg);
						transform: rotate(180deg);
					}
				}
			}
		}
	}
}

@media only screen and (min-width: 992px) and (max-width: 1162px) {
	.horizontalMenu > .horizontalMenu-list > li {
		> a {
			> .arrow {
				display: none;
			}

			padding-left: 12px;
			padding-right: 12px;
		}

		a.menuhomeicon {
			padding-left: 22px;
			padding-right: 22px;
		}
	}
}

.horizontalMenu-list {
	.sub-menu:before,
	.halfdiv:before {
		position: absolute;
		top: -6px;
		left: 12px;
		display: inline-block;
		border-right: 5px solid transparent;
		border-bottom: 5px solid $border;
		border-left: 5px solid transparent;
		border-bottom-color: $black-2;
		content: "";
	}

	.sub-menu {
		.sub-menu {
			&:before,
			&:after {
				display: none;
			}
		}

		&:after {
			position: absolute;
			top: -5px;
			left: 12px;
			display: inline-block;
			border-right: 5px solid transparent;
			border-bottom: 5px solid $white;
			border-left: 5px solid transparent;
			content: "";
		}
	}

	.halfdiv:after {
		position: absolute;
		top: -5px;
		left: 12px;
		display: inline-block;
		border-right: 5px solid transparent;
		border-bottom: 5px solid $white;
		border-left: 5px solid transparent;
		content: "";
	}

	.sub-menu {
		&:before,
		&:after {
			left: 12px;
			right: auto;
		}
	}

	.halfdiv {
		&:before,
		&:after {
			left: 12px;
			right: auto;
		}
	}
}

@media (max-width: 992px) {
	.horizontalMenu > .horizontalMenu-list > li > .horizontal-megamenu .megamenu-content {
		border: 0;
	}
}

.horizontalMenu ul li a.btn {
	font-size: 14px !important;
}
.horizontalMenu > .horizontalMenu-list > li > ul.sub-menu > li > ul.sub-menu > li:hover > a i {
	color: $white;
}
@media only screen and (max-width: 991px) {
	.horizontalMenu>.horizontalMenu-list {
		border-right: 1px solid $border;
	}
} 
@media only screen and (max-width: 991px) {
	.rtl .horizontalMenu>.horizontalMenu-list {
		border-left: 1px solid $border;
		border-right: 0;
	}
} 
