}

.btn-block {
	display: block;
	width: 100%;

	+.btn-block {
		margin-top: 0.5rem;
	}
}

input {

	&[type="button"].btn-block,
	&[type="reset"].btn-block,
	&[type="submit"].btn-block {
		width: 100%;
	}
}

.fade {
	transition: opacity 0.15s linear;

	&:not(.show) {
		opacity: 0;
	}
}

@media screen and (prefers-reduced-motion: reduce) {
	.fade {
		transition: none;
	}
}

.collapse:not(.show) {
	display: none;
	background: $white;
}

.collapsing {
	position: relative;
	height: 0;
	overflow: hidden;
	transition: height 0.35s ease;
}

@media screen and (prefers-reduced-motion: reduce) {
	.collapsing {
		transition: none;
	}
}

.btn {
	cursor: pointer;
	font-weight: 400;
	letter-spacing: 0.03em;
	font-size: 0.8725rem;
	min-width: 2.375rem;
}

.btn-icon {
	padding-left: 0.5rem;
	padding-right: 0.5rem;
	text-align: center;
}

.btn.btn-app {
	position: relative;
	padding: 15px 5px;
	margin: 0 0 10px 10px;
	min-width: 80px;
	height: 60px;
	box-shadow: none;
	border-radius: 0;
	text-align: center;
	color: $color;
	border: 1px solid $border;
	background-color: $light;
	font-size: 12px;

	> {

		.fa,
		.glyphicon,
		.ion {
			font-size: 20px;
			display: block;
		}
	}

	&:hover {
		background: $light;
		color: $color2;
		border-color: $border;
	}

	&:active,
	&:focus {
		box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
	}

	>.badge {
		position: absolute;
		top: -3px;
		right: -10px;
		font-weight: 400;
	}
}

.btn-pill {
	border-radius: 10rem;
	padding-left: 1.5em;
	padding-right: 1.5em;
}

.btn-square {
