.table .thead-dark th,
.text-wrap table .thead-dark th {
	color           : $background;
	background-color: $color;
	border-color    : #32383e;
}

.table .thead-light th,
.text-wrap table .thead-light th {
	color           : #3d4e67;
	background-color: $border;
	border-color    : $border;
}

.table-dark {
	color           : $background;
	background-color: $color;

	td,
	th,
	thead th {
		border-color: #32383e;
	}

	&.table-bordered {
		border: 0;
	}
}

.text-wrap table.table-dark {
	border: 0;
}

.table-dark {
	&.table-striped tbody tr:nth-of-type(odd) {
